.add-to-cart-btn {
    background-color: orange;
    border: none;
    border-radius: 20px;
    outline: none;
    margin-top: 10px;
}

.shopping-item {
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 5px;
}

.item-img {
    width: 60%;
    margin-top: 5px;
}

.price-details {
    font-size: 14px;
    margin-top: 5px;
    font-weight: bold;
}

.actual {
    color: black;
    text-align: right;
}

.display {
    color: gray;
    text-decoration: line-through;
    text-align: center;
}

.discount {
    color: green;
    text-align: left;
}

